<template>
  <div class="container">
    <!-- <div class="dropdown" @click="handlerDropmenu"> -->
    <div class="dropdown">
      <div v-if="isDropmenuOpen">
        <div class="title-dropmenu">
          <span>All dashboards</span>
          <img src="../assets/images/dropmenu/drop-up.svg" alt="drop-up" />
        </div>
        <div class="dashboard-list">
          <ul>
            <li
              class="item-list"
              v-for="dList in dashboardList"
              :key="dList.id"
            >
              <div class="circle"></div>
              <span @click="CHANGE_DASH(dList)">{{ dList.name }}</span>
              <div class="dashb-copy" @click="COPY_DASH(dList)"></div>
              <div class="dashb-delete" @click="DELETE_DASH(dList)"></div>
            </li>
          </ul>
          <div class="btn-add-dashboard" @click="ADD_NEW_DASH"></div>
        </div>
      </div>
      <div v-else>
        <div class="title-dropmenu">
          <span>{{ selectedDashboard.name }}</span>
          <img src="../assets/images/dropmenu/drop-down.svg" alt="drop-down" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations, mapGetters, mapActions } from "vuex";
// import api from "@/api/settings";
export default {
  name: "DashboardDropmenu",
  data() {
    return {
      isDropmenuOpen: false
    };
  },
  computed: {
    ...mapState("dashboards", ["dashboardList"]),
    ...mapState("auth", ["token"]),
    ...mapGetters("dashboards", ["selectedDashboard"])
  },
  methods: {
    ...mapMutations("dashboards", [
      "DELETE_DASH",
      "CHANGE_DASH",
      "ADD_NEW_DASH",
      "COPY_DASH",
      "LOAD_DASH"
    ]),
    ...mapActions("Alert", ["GET_ALERTS"]),
    ...mapActions("Tip", ["GET_TIPS"]),
    handlerDropmenu(event) {
      const target = event.target;
      if (
        target.classList.contains("dashb-delete") ||
        target.classList.contains("dashb-copy") ||
        target.classList.contains("btn-add-dashboard")
      ) {
        return;
      }
      this.isDropmenuOpen = !this.isDropmenuOpen;
      const curRoute = this.$router.history.current.name;
      curRoute !== "DashLayout" ? this.$router.push("/dashboard") : false;
    }
  }
};
</script>

<style lang="scss" scoped>
.container {
  position: relative;
  width: 30rem;
  height: 5.5rem;
}

.dropdown {
  position: absolute;
  width: 30rem;
  display: flex;
  flex-direction: column;
  user-select: none;
  font-size: 1.8rem;
  background-color: #262626;
  padding: 1.2rem;
  border-radius: 2.5rem;
  opacity: 0.7;
  z-index: 100;
}

.dropdown-close {
  color: #ffffff;
}

.title-dropmenu {
  color: #ffffff;
  display: flex;
  justify-content: center;
  font-size: 2.2rem;
  cursor: pointer;

  img {
    width: 15%;
    margin-left: 2rem;
  }
}

.dropdown:hover {
  box-shadow: 0.4rem 0.4rem 0.7rem 0.1rem rgba(0, 0, 0, 0.3),
    -0.3rem -0.3rem 0.7rem 0.1rem rgba(255, 255, 255, 0.2);
  opacity: 1;
}

.dashboard-list {
  margin: 1.5rem 0rem 0rem 1rem;
  background-color: #262626;
}

.item-list {
  display: flex;
  align-items: center;
  color: #ffffff;
  font-size: 2rem;
  cursor: pointer;
}

.item-list:hover {
  color: #7873ff;
}

.item-list span {
  flex-grow: 1;
  text-align: start;
}

.circle {
  background-color: #ef5466;
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  margin-right: 1rem;
}

.dashb-copy {
  width: 2.2rem;
  height: 2.2rem;
  background-image: url("../assets/images/dropmenu/dashb-icon.svg");
  background-size: cover;
  margin: 0rem 1rem;
  cursor: pointer;
}

.dashb-delete {
  width: 1.8rem;
  height: 1.8rem;
  background-image: url("../assets/images/dropmenu/delete-cross.svg");
  background-size: cover;
  cursor: pointer;
}

.btn-add-dashboard {
  width: 3.4rem;
  height: 3.4rem;
  margin: 1rem auto;
  background-image: url("../assets/images/dropmenu/new-dashboard.svg");
  background-size: cover;
  color: #ffffff;
  cursor: pointer;
}
</style>
