export default {
  getAlerts: async token => {
    const body = { token: token };
    const result = await fetch("https://melytix.herokuapp.com/get-alerts", {
      method: "POST",
      headers: {
        "Content-Type": "application/json;charset=utf-8"
      },
      body: JSON.stringify(body)
    })
      .then(res => res.json())
      .then(data => {
        return data;
      });
    return result;
  },

  getTips: async token => {
    const body = { token: token };
    const result = await fetch("https://melytix.herokuapp.com/get-tips", {
      method: "POST",
      headers: {
        "Content-Type": "application/json;charset=utf-8"
      },
      body: JSON.stringify(body)
    })
      .then(res => res.json())
      .then(data => {
        return data;
      });
    return result;
  },

  changeIsActive: async ({ token, id, type }) => {
    const body = { token, id, type };
    const result = await fetch("https://melytix.herokuapp.com/flip", {
      method: "POST",
      headers: {
        "Content-Type": "application/json;charset=utf-8"
      },
      body: JSON.stringify(body)
    })
      .then(res => res.json())
      .then(data => {
        return data;
      });
    return result;
  }
};
