import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Auth",
    component: () => import("../views/Auth.vue"),
    beforeEnter: (to, from, next) => {
      const auto_auth = localStorage.getItem("token") || "";
      if (auto_auth) {
        sessionStorage.setItem("token", auto_auth);
        next({ name: "DashLayout" });
      } else {
        next();
      }
    }
  },
  {
    path: "/dashboard",
    name: "DashLayout",
    component: () => import("../components/DashLayout.vue")
  },
  {
    path: "/profile",
    name: "systemProfile",
    component: () => import("../views/SystemProfile.vue")
  },
  {
    path: "/add-member",
    name: "AddMember",
    component: () => import("../views/AddMember.vue")
  },
  {
    path: "/SystemToolTip",
    name: "Tips",
    component: () => import("../views/SystemTooltip.vue")
  },
  // {
  //   path: "/auth",
  //   name: "Auth",
  //   component: () => import("../views/Auth.vue")
  // },
  {
    path: "/Alerts",
    name: "Alerts",
    component: () => import("../views/Alerts.vue")
  },
  {
    path: "/Support",
    name: "Support",
    component: () => import("../views/Support.vue")
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
