export default {
  fetchAnalytics: async token => {
    const body = { token: token };
    try {
      const result = await fetch(
        "https://melytix.herokuapp.com/get-select-data",
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json;charset=utf-8"
          },
          body: JSON.stringify(body)
        }
      )
        .then(res => res.json())
        .then(data => {
          return data;
        });
      console.log(result);
      return result;
    } catch ({ message }) {
      return { Error: message };
    }
  },
  sendSelectedAccount: async options => {
    console.log("options", options);
    try {
      const result = await fetch(
        "https://melytix.herokuapp.com/insert-viewid",
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json;charset=utf-8"
          },
          body: JSON.stringify(options)
        }
      )
        .then(res => res.json())
        .then(data => {
          return data;
        });
      return result;
    } catch ({ message }) {
      return { Error: message };
    }
  },
  fetchGSC: async token => {
    const body = { token: token };
    try {
      const result = await fetch(
        "https://melytix.herokuapp.com/get-sites-url",
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json;charset=utf-8"
          },
          body: JSON.stringify(body)
        }
      )
        .then(res => res.json())
        .then(data => {
          return data;
        });
      console.log(result);
      return result;
    } catch ({ message }) {
      return { Error: message };
    }
  },

  sendAccAndRes: gaData => {
    return fetch("https://melytix.herokuapp.com/get-ga-data", {
      method: "POST",
      headers: {
        "Content-Type": "application/json;charset=utf-8"
      },
      body: JSON.stringify(gaData)
    }).then(res => res.json());
  },

  sendGSCData: scData => {
    return fetch("https://melytix.herokuapp.com/get-sc-data", {
      method: "POST",
      headers: {
        "Content-Type": "application/json;charset=utf-8"
      },
      body: JSON.stringify(scData)
    }).then(res => res.json());
  }
};
