import api from "@/api/auth";
import router from "@/router/index";

export default {
  namespaced: true,
  state: {
    authData: {
      email: "",
      password: ""
    },
    token: sessionStorage.getItem("token") || "",
    email: localStorage.getItem("email") || "",
    isMessage: false,
    message: "",
    isLogin: false,
    user: "",
    forgotPassStatus: false,
    isFetching: false,
    authTemplate: {
      email: "",
      password: ""
    }
  },
  mutations: {
    LOGOUT: state => {
      sessionStorage.removeItem("token");
      localStorage.removeItem("token");
      localStorage.removeItem("email");
      localStorage.removeItem("dashboardList");
      state.isLogin = false;
      state.isAnalyticsAuth = false;
      state.token = "";
      state.email = "";
      state.message = "";
      state.authData = Object.assign({}, state.authTemplate);
    },
    SAVE_LOGIN: (state, { token, save_token }) => {
      state.isFetching = false;
      sessionStorage.setItem("token", token);
      if (save_token) localStorage.setItem("token", token);
      localStorage.setItem("email", state.authData.email);
      state.email = state.authData.email;
      state.token = token;
      state.isLogin = true;
      state.authData = Object.assign({}, state.authTemplate);
      return;
    },
    SAVE_GOOGLE_LOGIN: (state, service) => {
      state.isFetching = false;
      state[service] = true;
      localStorage.setItem(service, true);
      console.log("service", service);
      return;
    },
    GOOGLE_LOGIN_FAIL: (state, { Error }) => {
      state.message = Error;
      state.isMessage = true;
      return;
    },
    FACEBOOK_LOGIN: (state, data) => {
      const { token, Error } = data;
      if (Error) {
        state.message = Error;
        state.isMessage = true;
        return;
      }
      if (token) {
        sessionStorage.setItem("token", token);
        state.token = token;
        state.isLogin = true;
        return;
      }
    },
    DEL_AUTHMESSAGE: state => {
      state.message = "";
      state.isMessage = false;
      return;
    },
    ON_FETCHING: state => {
      state.isFetching = true;
      return;
    },
    OFF_FETCHING: state => {
      state.isFetching = false;
      return;
    },
    SET_NEWPASS_STATUS: (state, isNewPass) => {
      state.forgotPassStatus = isNewPass;
      return;
    },
    SET_ERROR: (state, error) => {
      if (error == "user not found" || error == "wrong password") {
        error = "    !";
      }
      state.message = error;
      state.isMessage = true;
      return;
    }
  },
  actions: {
    LOGIN: async ({ commit, state }, save_token) => {
      const answ = await api.fetchLogin(state.authData).then(data => {
        return data;
      });
      const { Error, token } = answ;
      if (Error) {
        commit("SET_ERROR", Error);
        commit("OFF_FETCHING");
        commit("OFF_ISLOADING", answ, { root: true });
        return;
      }
      if (token) {
        commit("SAVE_LOGIN", { token, save_token });
        commit("OFF_FETCHING");
        commit("OFF_ISLOADING", answ, { root: true });
        router.push({ name: "DashLayout" });
        return;
      }
    },
    FORGOTPASS: ({ commit }, email) => {
      const isNewPass = api.forgotPass(email);
      commit("SET_NEWPASS_STATUS", isNewPass);
      return;
    },
    GOOGLE_LOGIN: async ({ commit, dispatch }, { code, service }) => {
      const token = sessionStorage.getItem("token");
      const authData = { code, token };
      const answ = await api.googleAuth(authData).then(
        data => {
          return data;
        },
        e => commit("GOOGLE_LOGIN_FAIL", e)
      );
      const { Message, Error } = answ;
      if (Error) {
        commit("GOOGLE_LOGIN_FAIL", answ);
        return;
      }
      if (Message == "Success") {
        dispatch("GLOBAL_ADD_SERVICE", service, { root: true });
        const DISPATCH_NAME = "GLOBAL_GET_SERVICE_" + service;
        dispatch(DISPATCH_NAME, token, { root: true });
        return;
      }
    }
  },
  getters: {
    authData() {
      return this.authData;
    },
    token(state) {
      return state.token;
    },
    message(state) {
      return state.message;
    },
    user(state) {
      return state.user;
    }
  }
};
