<template>
  <div id="nav-bar">
    <router-link to="/dashboard">
      <div class="nav-btn">
        <img src="../assets/images/left-nav-bar/widgets.svg" alt="widgets" />
      </div>
    </router-link>
    <router-link to="/SystemToolTip">
      <div class="nav-btn">
        <img src="../assets/images/left-nav-bar/tip.svg" alt="tip" />
      </div>
    </router-link>
    <router-link to="/Alerts">
      <div class="nav-btn">
        <img src="../assets/images/left-nav-bar/alert.svg" alt="alert" />
      </div>
    </router-link>
    <div class="nav-btn" @click="handlerSupport">
      <img src="../assets/images/left-nav-bar/support.svg" alt="support" />
    </div>
    <!-- <router-link to="/add-member">
      <div class="nav-btn">
        <img src="../assets/images/left-nav-bar/add-member.svg" alt="add-member" />
      </div>
    </router-link>
    <div class="nav-btn">
      <img src="../assets/images/left-nav-bar/support.svg" alt="support" />
    </div> -->
    <div class="flex-spacer"></div>
    <div class="nav-btn last-item">
      <img
        src="../assets/images/left-nav-bar/exit.svg"
        alt="exit"
        @click="handlerLogout"
      />
    </div>
  </div>
</template>

<script>
import { mapMutations } from "vuex";
export default {
  name: "NavBar",
  components: {},
  methods: {
    ...mapMutations("auth", ["LOGOUT"]),
    ...mapMutations("dashboards", ["CLEAR_DASHBOARD_LIST"]),
    ...mapMutations("Alert", ["CLEAR_ALERTS"]),
    ...mapMutations("servicesSelect", ["SET_ADD_WIDGET"]),
    handlerLogout() {
      this.LOGOUT();
      // this.CLEAR_DASHBOARD_LIST();
      this.CLEAR_ALERTS();
      this.$router.push({ name: "Auth" });
      this.isOpen = !this.isOpen;
    },
    handlerSupport() {
      this.SET_ADD_WIDGET(false);
      const path = "/Support";
      if (this.$route.path !== path) this.$router.push(path);
    }
  }
};
</script>

<style lang="scss" scoped>
#nav-bar {
  height: calc(100vh - 10rem);
  width: 9rem;
  background-color: #1c1c1c;
  grid-area: nav_bar;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.nav-btn {
  width: 5.2rem;
  height: 5.2rem;
  border-radius: 1.5rem;
  margin-top: 3rem;
  background-color: #262626;
  opacity: 0.7;
  display: flex;
  align-items: center;
  justify-content: center;
  user-select: none;

  img {
    width: 60%;
    height: 60%;
  }
}

.last-item {
  margin-bottom: 3rem;
}

.nav-btn:active {
  background-color: #202020;
}

.nav-btn:hover {
  box-shadow: 0.4rem 0.4rem 0.7rem 0.1rem rgba(0, 0, 0, 0.3),
    -0.3rem -0.3rem 0.7rem 0.1rem rgba(255, 255, 255, 0.2);
  opacity: 1;
}

.flex-spacer {
  flex-grow: 1;
}
</style>
