<template>
  <div class="days-picker">
    <table>
      <thead>
        <th>Mon</th>
        <th>Tue</th>
        <th>Wed</th>
        <th>Thu</th>
        <th>Fri</th>
        <th>Sat</th>
        <th>Sun</th>
      </thead>
      <tbody>
        <tr v-for="(week, i) in data.calendar" :key="i">
          <td
            v-for="day in week"
            :key="day.dateInMs"
            :class="
              !day.isMonth ? `not-day-month` : day.isDay ? `day-select` : ``
            "
            @click="
              data.handlerClick({
                dateInMs: day.dateInMs,
                calendarName: data.dName
              })
            "
          >
            {{ day.num }}
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  name: "Calendar",
  props: ["data"],
  methods: {
    akjsdjsad() {}
  }
};
</script>

<style scoped>
.days-picker {
  position: absolute;
  z-index: 10;
  right: 5rem;
  width: 38rem;
  /* height: 20rem; */
  background-color: #262626;
  box-shadow: 0rem 0.3rem 0.5rem 0.2rem rgba(255, 255, 255, 0.2);
  color: #ffffff;
  line-height: 200%;
  border-radius: 1.5rem;
}

.days-picker table {
  width: 100%;
}

.days-picker table th {
  width: calc(38rem / 7);
}

.not-day-month {
  opacity: 0.3;
}

.days-picker table td {
  cursor: pointer;
}

.day-select {
  color: white;
  background: url("../../assets/images/datepicker/bg-select-day1.svg") no-repeat
      center,
    url("../../assets/images/datepicker/bg-select-day2.svg") no-repeat center;
}
</style>
