import services from "@/services/interactiveDashboards";

export default {
  namespaced: true,
  state: {
    dashboardList: [
      // {
      //   id: "1",
      //   isSelected: true,
      //   name: "Page 1",
      //   arrWidgets: []
      // },


      // //////// Static dashboard
      {
        id: "1",
        isSelected: true,
        name: "My dashboard",
        arrWidgets: [
          {
            name: "Static-LastTip",
            pos: { x: 0, y: 0 },
            size: { cols: 6, rows: 2 },
            isSelected: false,
            data: {}
          },
          {
            name: "Static-SumTips",
            pos: { x: 6, y: 0 },
            size: { cols: 2, rows: 2 },
            isSelected: false,
            data: {}
          },
          {
            name: "Static-LastAlert",
            pos: { x: 0, y: 2 },
            size: { cols: 6, rows: 2 },
            isSelected: false,
            data: {}
          },
          {
            name: "Static-SumAlerts",
            pos: { x: 6, y: 2 },
            size: { cols: 2, rows: 2 },
            isSelected: false,
            data: {}
          }
        ]
      }


      // {
      //   id: "2",
      //   isSelected: false,
      //   name: "My dashboard",
      //   arrWidgets: [
      //     {
      //       name: "signups_1",
      //       pos: { x: 3, y: 0 },
      //       size: { cols: 3, rows: 4 },
      //       isSelected: false,
      //       data: {
      //         dates: [
      //           "2020-08-01",
      //           "2020-08-02",
      //           "2020-08-03",
      //           "2020-08-04",
      //           "2020-08-05",
      //           "2020-08-06",
      //           "2020-08-07"
      //         ],
      //         data: [1, 5, 9, 3, 12, 5, 7]
      //       }
      //     },
      //     {
      //       name: "revenu_1",
      //       pos: { x: 0, y: 0 },
      //       size: { cols: 3, rows: 4 },
      //       isSelected: false,
      //       data: {
      //         dates: [
      //           "2020-08-01",
      //           "2020-08-02",
      //           "2020-08-03",
      //           "2020-08-04",
      //           "2020-08-05",
      //           "2020-08-06",
      //           "2020-08-07"
      //         ],
      //         metric: [1, 5, 9, 3, 12, 5, 7]
      //       }
      //     }
      //   ]
      // }
    ]
  },
  mutations: {
    LOAD_DASH: (state, dashLoad) => {
      const noDash = [
        {
          id: "1",
          isSelected: true,
          name: "Page 1",
          arrWidgets: []
        }
      ];
      state.dashboardList = dashLoad ? dashLoad : noDash;
    },
    CLEAR_DASHBOARD_LIST: state => {
      state.dashboardList = [
        {
          id: "1",
          isSelected: true,
          name: "Page 1",
          arrWidgets: []
        }
      ];
    },
    DELETE_DASH: (state, dash) => {
      state.dashboardList = state.dashboardList.filter(el => el.id != dash.id);

      if (!state.dashboardList.length) {
        const clearDashboard = {
          id: "0",
          isSelected: true,
          name: "Page 1",
          arrWidgets: []
        };
        state.dashboardList.push(clearDashboard);
      } else if (dash.isSelected) {
        state.dashboardList[0].isSelected = true;
      }
    },

    CHANGE_DASH: (state, dash) => {
      state.dashboardList = state.dashboardList.map(d => {
        d.isSelected = false;
        if (d.id === dash.id) d.isSelected = true;
        return d;
      });
    },

    ADD_NEW_DASH: state => {
      const len = state.dashboardList.length;
      state.dashboardList.push({
        id: len + 1,
        name: `Page ${len + 1}`,
        arrWidgets: []
      });
      state.dashboardList.forEach((el, i) => (el.id = i));
    },

    COPY_DASH: (state, dash) => {
      const len = state.dashboardList.length;
      state.dashboardList.push({
        id: len + 1,
        name: `Page ${len + 1}`,
        arrWidgets: dash.arrWidgets
      });
      state.dashboardList.forEach((el, i) => (el.id = i));
    },

    CHANGE_WIDGET_POSITION: (state, arrWidgets) => {
      state.dashboardList.find(d => {
        if (d.isSelected) {
          d.arrWidgets.forEach(w => {
            const newW = arrWidgets.find(nW => nW.name === w.name);
            w.pos = newW.pos;
            w.size = newW.size;
          });
        }
      });
      state.dashboardList = state.dashboardList.concat();
    },

    ADD_NEW_WIDGET: (state, data) => {
      const selectedDash = state.dashboardList.find(d => d.isSelected);
      let gridArea = services.getGridArea(selectedDash.arrWidgets);

      gridArea = selectedDash.arrWidgets.length
        ? gridArea
        : services.deleteEmptyWidget(gridArea);

      // create a unique name for widget
      const wData = {
        name: "",
        cols: 1,
        rows: 1
      };
      let num = selectedDash.arrWidgets.length;

      do {
        wData.name = data.metric + "_" + num++;
      } while (selectedDash.arrWidgets.find(w => w.name === wData.name));
      wData.cols = data.size ? data.size.cols : 3;
      wData.rows = data.size ? data.size.rows : 4;

      let res = false;

      for (let i = 0; i <= gridArea.length - wData.rows; i++) {
        for (let j = 0; j <= gridArea[i].length - wData.cols; j++) {
          if (!services.isEmptyPosition(wData, gridArea, { row: i, col: j })) {
            !res ? (res = { x: j, y: i }) : false;
            break;
          }
        }
      }
      if (res) {
        const newWidget = {
          name: wData.name,
          pos: { x: res.x, y: res.y },
          size: { cols: wData.cols, rows: wData.rows },
          data: data.dataFromSer
        };
        state.dashboardList = state.dashboardList.map(dash => {
          if (dash === selectedDash) {
            dash.arrWidgets.push(newWidget);
          }
          return dash;
        });
      }
    },

    DELETE_WIDGET: (state, wName) => {
      state.dashboardList = state.dashboardList.map(dash => {
        dash.arrWidgets = dash.arrWidgets.filter(w => w.name != wName);
        return dash;
      });
    },

    SELECT_WIDGET: (state, wName) => {
      const selDash = state.dashboardList.find(d => d.isSelected);

      selDash.arrWidgets = selDash.arrWidgets.map(w => {
        if (w.name === wName) {
          w.isSelected = !w.isSelected;
        } else {
          w.isSelected = false;
        }
        return w;
      });
      state.dashboardList = state.dashboardList.concat();
    }
  },
  actions: {
    GLOBAL_ADD_NEW_WIDGET: {
      root: true,
      handler({ commit }, payload) {
        commit("ADD_NEW_WIDGET", payload);
      }
    }
  },
  getters: {
    selectedDashboard: state => {
      return state.dashboardList.find(dash => dash.isSelected);
    },

    getSelectedWidget: state => {
      const selDash = state.dashboardList.find(dash => dash.isSelected);
      return selDash.arrWidgets.find(w => w.isSelected);
    }
  }
};
