<template>
  <div id="google-search-btn" @click="click">
    <span>Google Search Console</span>
    <img
      src="@/assets/images/search/google_auth.png"
      alt="google-search"
      draggable="false"
    />
  </div>
</template>
<script>
import { mapMutations, mapState, mapActions } from "vuex";
export default {
  name: "GoogleSearchConsole",
  props: ["isAdd"],
  data() {
    return {
      isAdded: this.$props.isAdd,
      clientId:
        "380470694344-0a4vb8rvio43bje2dmbs5hk7l8ecdglm.apps.googleusercontent.com",
      scope:
        "https://www.googleapis.com/auth/userinfo.profile https://www.googleapis.com/auth/webmasters.readonly",
      gapi: window.gapi,
      name: "GoogleSearchConsole"
    };
  },
  computed: {
    ...mapState("auth", ["isGoogleSearchConsole", "token"]),
    ...mapState("servicesSelect", ["isAddWidget"])
  },
  methods: {
    ...mapMutations(["OPEN_MODAL", "SET_ISWIDGETSETTINGS"]),
    ...mapMutations("servicesSelect", ["SET_ADD_WIDGET"]),
    ...mapMutations("auth", ["GOOGLE_LOGIN_FAIL"]),
    ...mapActions("auth", ["GOOGLE_LOGIN"]),
    click() {
      console.log("click", this.name);
      if (this.isAdded == "add" && this.isAddWidget) {
        this.OPEN_MODAL();
        this.SET_ADD_WIDGET(false);
        this.SET_ISWIDGETSETTINGS(this.name);
      }
    },
    prompt({ code }) {
      this.GOOGLE_LOGIN({ code, service: this.name });
    },
    onSuccess(gu) {
      const options = {
        client_id: this.clientId,
        scope: this.scope,
        prompt: "consent"
      };
      gu.grantOfflineAccess(options).then(this.prompt, this.GOOGLE_LOGIN_FAIL);
    },
    init() {
      this.gapi.load("auth2", () => {
        const GoogleAuth = this.gapi.auth2.init({
          client_id: this.clientId
        });
        const btn = document.querySelector("#google-search-btn");
        GoogleAuth.attachClickHandler(
          btn,
          {},
          this.onSuccess,
          this.GOOGLE_LOGIN_FAIL
        );
      });
    }
  },
  mounted() {
    if (this.isAdded == "notadd") {
      this.init();
    }
  }
};
</script>
<style scoped>
#google-search-btn {
  width: calc((100% / 3) - 30px);
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.5vw;
  padding: 1.5vw 0;
  color: #ffffff;
  cursor: pointer;
  transition: all 0.1s linear;
  user-select: none;
  margin: 10px 0 0 10px;
  background-color: #262626;
  box-shadow: 0 0 5px 0.1px #f2f2f2;
  opacity: 0.7;
}
#google-search-btn img {
  height: 2vw;
  margin-left: 10px;
}
#google-search-btn:hover {
  box-shadow: 0 0 7px 1px #f2f2f2;
  opacity: 1;
}
#google-search-btn:active {
  box-shadow: inset 0 0 5px 0.1px #f2f2f2;
  transform: scale(0.95);
  opacity: 1;
}
</style>
