/* eslint-disable prettier/prettier */
<template>
  <div class="modal-widget">
    <div class="header">
      <div class="arrow-back" @click="handlerBack"></div>
      <!-- <ol class="crumbs">
        <li v-if="widgetType === 'GoogleAnalytics'">GoogleAnalytics ></li>
        <li v-else-if="widgetType === 'GoogleSearchConsole'">
          GoogleSearchConsole >
        </li>
        <li>Settings</li>
      </ol> -->
      <div class="btn-close" @click="CLOSE_MODAL"></div>
    </div>
    <div class="content">
      <aside class="left">
        <div class="widget"></div>
      </aside>
      <aside class="right">
        <div class="settings">
          <div class="source-blc">
            <table>
              <tbody v-if="widgetType === 'GoogleAnalytics'">
                <tr>
                  <td>System:</td>
                  <td>Google Analytics</td>
                </tr>
                <tr>
                  <td>Account:</td>
                  <td id="an-site">{{ AnalyticsProject.account.name }}</td>
                </tr>
                <tr>
                  <td>Web Property:</td>
                  <td id="name-site">
                    {{ AnalyticsProject.web_property.prop_name }}
                  </td>
                </tr>
              </tbody>
              <tbody v-else-if="widgetType === 'GoogleSearchConsole'">
                <tr>
                  <td>System:</td>
                  <td>Google Search Console</td>
                </tr>
                <tr>
                  <td>Site:</td>
                  <td id="an-site">{{ GSCUrl.url }}</td>
                </tr>
              </tbody>
            </table>
          </div>
          <div class="settings-blc">
            <div class="select-group">
              <label for="metrics">Metrics:</label>
              <select name="select1" id="metrics">
                <option v-for="(metric, i) in metrics" :key="i" :value="metric">
                  {{ metric }}
                </option>
              </select>
            </div>
            <div class="select-group">
              <label for="filters">Filters:</label>
              <select name="select2" id="filters" disabled>
                <option value="value1" selected>Text</option>
                <option value="value2">Text 2</option>
                <option value="value3">Text 3</option>
              </select>
            </div>
            <div class="select-group">
              <label for="types">Types:</label>
              <select name="select2" id="types" disabled>
                <option value="value1" selected>Text</option>
                <option value="value2">Text 2</option>
                <option value="value3">Text 3</option>
              </select>
            </div>
            <div class="select-group">
              <label for="periods">Time:</label>
              <select name="select2" id="periods" disabled>
                <option value="value1" selected>Text</option>
                <option value="value2">Text 2</option>
                <option value="value3">Text 3</option>
              </select>
            </div>
            <div class="select-group">
              <label for="periods">Goals:</label>
              <select name="select2" id="periods" disabled>
                <option value="value1" selected>Text</option>
                <option value="value2">Text 2</option>
                <option value="value3">Text 3</option>
              </select>
            </div>
          </div>
          <button class="btn-ok" @click="handlerOk">Ok</button>
        </div>
      </aside>
    </div>
  </div>
</template>

<script>
import { mapMutations, mapState, mapActions } from "vuex";
export default {
  data() {
    return {
      projectName: "",
      webProperties: [],
      metrics: [],
      arrSourseName: [],
      AnalyticsProject: {},
      GSCUrl: ""
    };
  },
  computed: {
    ...mapState(["widgetType"]),
    ...mapState("servicesSelect", ["metricsGA", "metricsSC"]),
    ...mapState("dateRange", ["dateFrom", "dateTo"])
  },
  methods: {
    ...mapMutations(["CLOSE_MODAL", "BACK_TO_SERVICES"]),
    ...mapMutations("dashboards", ["ADD_NEW_WIDGET"]),
    ...mapMutations("servicesSelect", ["SET_ADD_WIDGET"]),
    ...mapActions("servicesSelect", ["SEND_GA_DATA", "SEND_GSC_DATA"]),

    handlerOk() {
      // const anSite = document.querySelector("#an-site");
      // const nameSite = document.querySelector("#name-site");
      const metrSelect = document.querySelector("#metrics");
      const fetchData = {};

      if (this.widgetType === "GoogleAnalytics") {
        fetchData.account = this.AnalyticsProject.account.id;
        fetchData.web_property = this.AnalyticsProject.web_property.prop_id;
        fetchData.start_date = this.getFormatDate(this.dateFrom);
        fetchData.end_date = this.getFormatDate(this.dateTo);
        fetchData.metric = metrSelect.value;
        this.SEND_GA_DATA(fetchData);
      } else if (this.widgetType === "GoogleSearchConsole") {
        fetchData.site_url = this.GSCUrl.url;
        fetchData.start_date = this.getFormatDate(this.dateFrom);
        fetchData.end_date = this.getFormatDate(this.dateTo);
        fetchData.metric = metrSelect.value;
        this.SEND_GSC_DATA(fetchData);
      }
      console.log(fetchData);
      this.CLOSE_MODAL();
    },

    handlerBack() {
      this.SET_ADD_WIDGET(true);
      this.BACK_TO_SERVICES();
    },

    getFormatDate(date) {
      const y = date.getFullYear();
      const m =
        date.getMonth() < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1;
      const d = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
      return `${y}-${m}-${d}`;
    }
  },
  // watch: {
  //   projectName(value) {
  //     if (this.widgetType === "GoogleAnalytics") {
  //       this.webProperties = this.AnalyticsProject.find(
  //         (el) => el.id == value
  //       ).webProperties;
  //     }
  //   },
  // },
  beforeMount() {
    const servList = JSON.parse(localStorage.getItem("added_services"));
    console.log(servList);
    if (this.widgetType === "GoogleAnalytics") {
      this.metrics = this.metricsGA;
      this.AnalyticsProject = servList.find(
        el => el.name === "GoogleAnalytics"
      ).selectedData;
    } else if (this.widgetType === "GoogleSearchConsole") {
      this.metrics = this.metricsSC;
      this.GSCUrl = servList.find(
        el => el.name === "GoogleSearchConsole"
      ).selectedData;
    }
  }
};
</script>

<style lang="scss" scoped>
.modal-widget {
  width: 100rem;
  height: 60rem;
  border-radius: 0.5rem;
  background-color: #1c1c1c;
  user-select: none;
}

.header {
  width: 100%;
  height: 6rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0rem 1rem;
}

.arrow-back {
  width: 8rem;
  height: 3rem;
  background: url("../assets/images/modal_widget_settings/arrow_back.svg")
    no-repeat center center / contain;
  cursor: pointer;
}

.arrow-back:hover {
  filter: drop-shadow(0rem 0rem 0.5rem rgb(250, 250, 250));
}

.crumbs {
  color: rgb(177, 177, 177);
  height: 3rem;
  line-height: 3rem;
  font-size: 2.2rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  flex-grow: 1;
}

.crumbs li {
  margin-right: 1rem;
  cursor: pointer;
}

.crumbs li:nth-child(2) {
  color: white;
}

.btn-close {
  width: 3rem;
  height: 3rem;
  background: url("../assets/images/modal_widget_settings/btn_close.svg")
    no-repeat center center / contain;
  margin-left: 2rem;
  cursor: pointer;
}
.btn-close:hover {
  filter: drop-shadow(0rem 0rem 0.5rem rgb(253, 2, 2));
}

.content {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: calc(100% - 6rem);
}

.left {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40%;
  height: 100%;
}

.right {
  display: flex;
  width: 60%;
  height: 100%;
}

.widget {
  margin: 11rem 0 0 6rem;
  width: 33.8rem;
  height: 35.8rem;
  background: url("../assets/images/widgets/graph.svg") no-repeat center center /
    cover;
  /* box-shadow: 0rem 0rem 1.5rem 0.5rem rgb(255, 255, 255, 0.2); */
  /* border-radius: 2rem; */
}

.settings {
  width: 100%;
}

.source-blc,
.settings-blc {
  display: flex;
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  // margin-top: 2rem;
  // border: 1px solid red;
}

.source-blc {
  margin-top: 1rem;
  table {
    color: white;
    font-size: 2.5rem;

    tr {
      line-height: 4.2rem;
      td:nth-child(1) {
        font-weight: 400;
        text-align: start;
      }
    }
  }
}

.settings-blc {
  margin-top: 4rem;
}

.select-group {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.select-group label {
  color: white;
  font-size: 2.5rem;
  width: 10rem;
}

.settings select {
  color: white;
  font-size: 2.2rem;
  background-color: #262626;
  width: 30rem;
  margin: 1rem;
  border-radius: 1rem;
  border: none;
  outline: none;
  padding-left: 1rem;
}

.btn-ok {
  color: white;
  font-weight: bold;
  background-color: #7873ff;
  width: 10rem;
  height: 3rem;
  border-radius: 0.5rem;
  border: none;
  outline: none;
  margin-top: 4rem;
  opacity: 0.7;
}

.btn-ok:hover {
  box-shadow: 0.4rem 0.4rem 0.7rem 0.1rem rgba(0, 0, 0, 0.3),
    -0.3rem -0.3rem 0.7rem 0.1rem rgba(255, 255, 255, 0.2);
  opacity: 1;
}
</style>
