function removeWidgetFromGrid(wName, grid) {
  return grid.map(row => {
    return row.map(cell => {
      cell === wName ? (cell = ".") : false;
      return cell;
    });
  });
}

function addWidgetOnGrid(widget, grid, pos) {
  const rowLen = grid.length;
  return grid.map((row, i) => {
    const colLen = row.length;
    return row.map((cell, j) => {
      if (pos.row + widget.rows > rowLen && pos.col + widget.cols > colLen) {
        if (i >= rowLen - widget.rows && j >= colLen - widget.cols) {
          cell = widget.name;
        }
      } else if (
        pos.row + widget.rows > rowLen &&
        j >= pos.col &&
        j < pos.col + widget.cols
      ) {
        if (i >= rowLen - widget.rows) {
          cell = widget.name;
        }
      } else if (
        pos.col + widget.cols > colLen &&
        i >= pos.row &&
        i < pos.row + widget.rows
      ) {
        if (j >= colLen - widget.cols) {
          cell = widget.name;
        }
      } else if (
        i >= pos.row &&
        i < pos.row + widget.rows &&
        j >= pos.col &&
        j < pos.col + widget.cols
      ) {
        cell = widget.name;
      }
      return cell;
    });
  });
}

function isEmptyPosition(widget, grid, pos) {
  return grid.find((row, i) => {
    return row.find((cell, j) => {
      if (
        i >= pos.row &&
        i < pos.row + widget.rows &&
        j >= pos.col &&
        j < pos.col + widget.cols &&
        cell !== "." &&
        cell !== widget.name
      ) {
        return true;
      }
    });
  });
}

function getGridLayoutData(target) {
  return {
    link: target,
    left: target.offsetLeft,
    top: target.offsetTop,
    width: target.offsetWidth,
    height: target.offsetHeight,
    colInPx: (target.offsetWidth - 55) / 12,
    rowInPx: (target.offsetHeight - 35) / 8
  };
}

function getWidgetData(ev, parent) {
  let target = ev.target;
  while (target.parentNode != parent.link) {
    target = target.parentNode;
  }
  return {
    link: target,
    name: target.dataset.name,
    left: target.offsetLeft,
    top: target.offsetTop,
    mouseX: ev.pageX - target.offsetLeft,
    mouseY: ev.pageY - target.offsetTop,
    width: target.offsetWidth,
    height: target.offsetHeight,
    cols: Math.round(target.offsetWidth / parent.colInPx),
    rows: Math.round(target.offsetHeight / parent.rowInPx)
  };
}

function getNewPosition(ev, widget, layout) {
  const newPosInPx = {
    x: ev.pageX - widget.mouseX,
    y: ev.pageY - widget.mouseY
  };
  return {
    row: Math.round(newPosInPx.y / layout.rowInPx) - 1,
    col: Math.round(newPosInPx.x / layout.colInPx) - 1
  };
}

function replaceWidgetWithShadow(grid, widget, shadow) {
  return grid.map(row => {
    return row.map(cell => {
      if (cell == widget.name) {
        cell = shadow.name;
      }
      return cell;
    });
  });
}

function inWidthLayout(ev, widget, layout) {
  return (
    ev.pageX - widget.mouseX + widget.width <= layout.width + layout.left + 5 &&
    ev.pageX - widget.mouseX >= layout.left
  );
}

function inHeightLayout(ev, widget, layout) {
  return (
    ev.pageY - widget.mouseY + widget.height <=
      layout.height + layout.top + 5 && ev.pageY - widget.mouseY >= layout.top
  );
}

function removeCssStyle(widget) {
  widget.style.position = "relative";
  widget.style.top = "";
  widget.style.left = "";
  widget.style.width = "";
  widget.style.height = "";
  widget.style.opacity = "1";
  widget.style.zIndex = "";
}

function addCssStyle(widget, { pageX, pageY }) {
  widget.link.style.position = "absolute";
  widget.link.style.width = widget.width + "px";
  widget.link.style.height = widget.height + "px";
  widget.link.style.top = pageY - widget.mouseY + "px";
  widget.link.style.left = pageX - widget.mouseX + "px";
  widget.link.style.opacity = "0.4";
  widget.link.style.zIndex = 99;
}

function getGridArea(arrWidgets) {
  const newGrid = [];
  for (let i = 0; i < 8; i++) {
    const row = [];
    for (let j = 0; j < 12; j++) {
      row[j] = ".";
    }
    newGrid.push(row);
  }

  if (arrWidgets.length) {
    arrWidgets.forEach(el => {
      for (let i = el.pos.y; i < el.pos.y + el.size.rows; i++) {
        for (let j = el.pos.x; j < el.pos.x + el.size.cols; j++) {
          newGrid[i][j] = el.name;
        }
      }
    });
  } else {
    for (let i = 0; i < 4; i++) {
      for (let j = 0; j < 2; j++) {
        newGrid[i][j] = "emptyWidget";
      }
    }
  }
  return newGrid;
}

function getArrWidgets(grid) {
  let arrWidgets = [];
  grid.forEach((row, i) => {
    row.forEach((cell, j) => {
      if (cell !== ".") {
        const widget = arrWidgets.find(el => el.name === cell);
        if (!widget) {
          const w = {
            name: cell,
            pos: { x: j, y: i },
            size: { cols: 1, rows: 1 }
          };
          arrWidgets.push(w);
        } else {
          widget.pos.x === j ? widget.size.rows++ : false;
          widget.pos.y === i ? widget.size.cols++ : false;
        }
      }
    });
  });
  return arrWidgets;
}

function deleteEmptyWidget(grid) {
  return grid.map(row => {
    return row.map(cell => {
      cell = ".";
      return cell;
    });
  });
}

export default {
  removeWidgetFromGrid,
  addWidgetOnGrid,
  isEmptyPosition,
  getWidgetData,
  getGridLayoutData,
  getNewPosition,
  replaceWidgetWithShadow,
  inWidthLayout,
  inHeightLayout,
  removeCssStyle,
  addCssStyle,
  getGridArea,
  getArrWidgets,
  deleteEmptyWidget
};
