// import randomDate from "@/services/RandomDateGen";
import api from "@/api/alerts&tips";

export default {
  namespaced: true,

  state: {
    recent: [],
    done: [],
    removed: []
  },
  mutations: {
    CLEAR_ALERTS: state => {
      state.recent = [];
      state.done = [];
      state.removed = [];
    },
    SAVE_ALERTS: (state, alerts) => {
      let recent = [];
      let removed = [];
      if (alerts) {
        alerts.map(e => {
          e.active ? recent.push(e) : removed.push(e);
        });
      }
      state.recent = recent.reverse();
      state.removed = removed.reverse();
    },
    CHANGE_RECENT: (state, remRecent) => {
      const answ = api.changeIsActive(remRecent);
      console.log(answ);
      let item = state.recent.find(el => el.id === remRecent.id);
      state.removed.push(item);
      state.recent = state.recent.filter(el => el.id !== remRecent.id);
    },
    CHANGE_DONE: () => {},
    CHANGE_REMOVED: async (state, item) => {
      const answ = await api.changeIsActive(item);
      console.log(answ);
      let toRecent = state.removed.find(el => el.id === item.id);
      state.recent.push(toRecent);
      state.removed = state.removed.filter(el => el.id !== item.id);
    }
  },
  actions: {
    GET_ALERTS: ({ commit }) => {
      const token = sessionStorage.getItem("token");
      api.getAlerts(token).then(data => {
        commit("SAVE_ALERTS", data);
      });
    }
  },
  getters: {}
};
