<template>
  <div :class="isAddWidget ? 'support-container-aw' : 'support-container'">
    <div class="header">
      <h3 v-if="!isAddWidget"> </h3>
      <h3 v-if="isAddWidget"> </h3>
      <div v-if="isAddWidget" class="btn-close" @click="close"></div>
    </div>
    <div class="content">
      <div class="recent block">
        <div class="title"></div>
        <div class="buttons-container">
          <div
            v-for="(button, index) in added_services"
            v-bind:key="index"
            v-bind:is="button.name"
            v-bind:isAdd="'add'"
          ></div>
        </div>
      </div>
      <div class="all block">
        <div class="title">  </div>
        <div class="buttons-container">
          <div
            v-for="(button, index) in not_added_services"
            v-bind:key="index"
            v-bind:is="button"
            class="button"
            v-bind:isAdd="'notadd'"
          ></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import GoogleAnalytics from "@/components/servicesSelect/googleAnalyticsBtn";
import GoogleSearchConsole from "@/components/servicesSelect/searchConsoleBtn";
import { mapState, mapMutations } from "vuex";
export default {
  name: "Support",
  data() {
    return {};
  },
  computed: {
    ...mapState("servicesSelect", [
      "added_services",
      "not_added_services",
      "isAddWidget"
    ])
  },
  components: {
    GoogleAnalytics,
    GoogleSearchConsole
  },
  methods: {
    ...mapMutations(["CLOSE_MODAL"]),
    ...mapMutations("servicesSelect", ["SET_ADD_WIDGET"]),
    close() {
      this.CLOSE_MODAL();
      this.SET_ADD_WIDGET(false);
      const path = "/dashboard";
      if (this.$route.path !== path) this.$router.push(path);
    }
  }
};
</script>

<style lang="scss" scoped>
.support-container {
  width: 100%;
  height: 100%;
  padding: 0 2vw;
  background-color: #1c1c1c;
  border-top-left-radius: 10px;
}
.support-container-aw {
  width: 70%;
  height: 80%;
  padding: 0 2vw;
  background-color: #1c1c1c;
}

.search-input {
  background-image: url("../assets/images/search/Vector.svg");
  background-repeat: no-repeat;
  background-size: 1.5vw;
  background-position: 0.5vw center;
  padding: 0 2.5vw;
  font-size: 1.5vw;
}

.buttons-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}

.block,
.search-input {
  margin-bottom: 2.5vw;
}

.header {
  width: 100%;
  font-size: 2.5vw;
  display: flex;
  align-items: center;
  color: white;
  font-weight: bold;
  padding: 0.75vw;
}

.btn-close {
  width: 1.5vw;
  height: 1.5vw;
  background: url("../assets/images/modal_widget_settings/btn_close.svg")
    no-repeat center center / contain;
  margin-left: auto;
  cursor: pointer;
}
.btn-close:hover {
  filter: drop-shadow(0px 0px 5px rgb(253, 2, 2));
}

.search-input {
  background-color: #262626;
  border: none;
  color: #ffffff;
}

.content {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.title {
  color: white;
  font-size: 2vw;
  width: 100%;
  border-bottom: 3px solid #7873ff;
  text-align: start;
}
</style>
