import api from "@/api/servicesSelect";

export default {
  namespaced: true,
  state: {
    added_services: JSON.parse(localStorage.getItem("added_services")) || [],
    not_added_services: JSON.parse(
      localStorage.getItem("not_added_services")
    ) || ["GoogleSearchConsole", "GoogleAnalytics"],
    isAddWidget: false,
    selectedService: "",
    currentData: {},
    signup: false,
    wData: null,
    metricsGA: [
      "ga_sessions",
      "ga_users",
      "ga_pageViews",
      "ga_pageViewsPerSession",
      "ga_avgSessionDuration",
      "ga_bounces",
      "ga_percentNewSessions"
    ],
    metricsSC: ["sc_clicks", "sc_impressions", "sc_ctr", "sc_position"]
  },

  mutations: {
    SAVE_ADDED_SERVICES: (state, component) => {
      const { not_added_services, added_services } = state;
      const names = added_services.map(e => {
        if (e.name) {
          return e.name;
        }
      });
      if (names.includes(component)) {
        return;
      }
      state.added_services = [...added_services, { name: component, data: [] }];
      const i = not_added_services.indexOf(component);
      not_added_services.splice(i, 1);
      state.not_added_services = [...not_added_services];
      localStorage.setItem(
        "added_services",
        JSON.stringify(state.added_services)
      );
      localStorage.setItem(
        "not_added_services",
        JSON.stringify(state.not_added_services)
      );
    },
    SET_ADD_WIDGET: (state, isAddWidget) => {
      state.isAddWidget = isAddWidget;
    },
    OPEN_SIGNUP: state => {
      state.signup = true;
    },
    CLOSE_SIGNUP: state => {
      state.signup = false;
    },
    SAVE_ANALYTICS: (state, data) => {
      const { added_services } = state;
      state.currentData = data;
      added_services.forEach(e => {
        e.name == "GoogleAnalytics" ? (e.data = data) : 0;
        return;
      });
      state.selectedService = "GoogleAnalytics";
      state.added_services = [...added_services];
      localStorage.setItem(
        "added_services",
        JSON.stringify(state.added_services)
      );
    },
    SAVE_DATA: (state, data) => {
      state.wData = data;
    },
    SAVE_GSC: (state, data) => {
      state.currentData = data;
      const { added_services } = state;
      added_services.forEach(e => {
        e.name == "GoogleSearchConsole" ? (e.data = data) : 0;
        return;
      });
      state.added_services = [...added_services];
      state.selectedService = "GoogleSearchConsole";
      localStorage.setItem(
        "added_services",
        JSON.stringify(state.added_services)
      );
    },
    SAVE_SELECTED_PROPERTY: (state, { name, data }) => {
      const { added_services } = state;
      added_services.forEach(e => {
        e.name == name ? (e.selectedData = data) : 0;
      });
      state.added_services = added_services;
      localStorage.setItem(
        "added_services",
        JSON.stringify(state.added_services)
      );
    }
  },

  actions: {
    SEND_GA_DATA: async ({ commit, dispatch }, gaData) => {
      gaData.token = sessionStorage.getItem("token");
      const dataFromSer = await api.sendAccAndRes(gaData);
      const renderData = {
        metric: gaData.metric,
        dataFromSer
      };
      commit("SAVE_DATA", renderData);
      dispatch("GLOBAL_ADD_NEW_WIDGET", renderData, { root: true });
    },

    SEND_GSC_DATA: async ({ commit, dispatch }, gscData) => {
      gscData.token = sessionStorage.getItem("token");
      const dataFromSer = await api.sendGSCData(gscData);
      const renderData = {
        metric: gscData.metric,
        dataFromSer
      };
      commit("SAVE_DATA", renderData);
      dispatch("GLOBAL_ADD_NEW_WIDGET", renderData, { root: true });
    },
    GET_GSC_DATA: async ({ commit }, token) => {
      const data = await api.fetchGSC(token);
      const { Error } = data;
      if (Error) {
        console.log(Error);
        return;
      }
      console.log("Data of GSC projects ", JSON.stringify(data));
      commit("SAVE_GSC", data.site_list);
      commit("SET_SELECT_PROPERTY", true, { root: true });
      commit("OPEN_MODAL", true, { root: true });
      commit("SET_ADD_WIDGET", false);
    },
    CLOSE_SELECT_PROPERTY: ({ commit }, name) => {
      commit("SAVE_SELECTED_PROPERTY", { name, data: [] });
    },
    GET_ANALYTICS_DATA: async ({ commit }, token) => {
      const data = await api.fetchAnalytics(token);
      const { Error } = data;
      if (Error) {
        console.log(Error);
        return;
      }
      console.log("Data of Analytics projects ", JSON.stringify(data));
      commit("SAVE_ANALYTICS", data);
      commit("SET_SELECT_PROPERTY", true, { root: true });
      commit("OPEN_MODAL", true, { root: true });
      commit("SET_ADD_WIDGET", false);
    },
    GLOBAL_GET_SERVICE_GoogleSearchConsole: {
      root: true,
      handler({ dispatch }, payload) {
        dispatch("GET_GSC_DATA", payload);
      }
    },
    GLOBAL_GET_SERVICE_GoogleAnalytics: {
      root: true,
      handler({ dispatch }, payload) {
        dispatch("GET_ANALYTICS_DATA", payload);
      }
    },
    GLOBAL_ADD_SERVICE: {
      root: true,
      handler({ commit }, payload) {
        commit("SAVE_ADDED_SERVICES", payload);
      }
    },
    ADD_SELECTED_PROPERTY: async ({ commit }, { name, body, data }) => {
      if (name === "GoogleAnalytics") {
        const result = await api.sendSelectedAccount(body);
        const { Error, Message } = result;
        if (Error) {
          console.log(Error);
          return;
        }
        if (Message) {
          console.log("add property");
          commit("SET_SELECT_PROPERTY", false, { root: true });
          commit("SAVE_SELECTED_PROPERTY", { name, data });
          commit("CLOSE_MODAL", true, { root: true });
          return;
        }
      }
      commit("SET_SELECT_PROPERTY", false, { root: true });
      commit("SAVE_SELECTED_PROPERTY", { name, data });
      commit("CLOSE_MODAL", true, { root: true });
    }
  }
};
