<template>
  <div class="main">
    <div class="profile-logo" @click="handlerProfile"></div>
    <div class="setting-dropmenu" v-if="isOpen">
      <ul class="list-items">
        <!-- <li @click="handlerSettings">Profile settings</li>
        <li>Upgrade</li>
        <li>Support</li>
        <li>Main Theme</li>
        <li>What`s new</li> -->
        <li @click="handlerLogout">Log Out</li>
      </ul>
    </div>
  </div>
</template>

<script>
import { mapMutations } from "vuex";
export default {
  name: "ProfileSettings",
  data() {
    return {
      isOpen: false
    };
  },
  methods: {
    ...mapMutations("auth", ["LOGOUT"]),
    ...mapMutations("dashboards", ["CLEAR_DASHBOARD_LIST"]),
    ...mapMutations("Alert", ["CLEAR_ALERTS"]),
    handlerProfile() {
      this.isOpen = !this.isOpen;
    },
    handlerSettings() {
      this.$router.push({ name: "systemProfile" });
      this.isOpen = false;
    },
    handlerLogout() {
      this.LOGOUT();
      // this.CLEAR_DASHBOARD_LIST();
      this.CLEAR_ALERTS();
      this.$router.push({ name: "Auth" });
      this.isOpen = !this.isOpen;
    }
  }
};
</script>

<style scoped>
a {
  text-decoration-line: none;
  color: #ffffff;
}

.main {
  margin: 0px 2rem 0rem 4rem;
  z-index: 999;
}

.profile-logo {
  width: 5.2rem;
  height: 5.2rem;
  border-radius: 50%;
  background-color: #000000;
  box-shadow: 0rem 0rem 0.5rem 0.3rem rgba(120, 115, 255, 0.3);
}

.setting-dropmenu {
  position: absolute;
  top: 8rem;
  right: 2rem;
  width: 15.3rem;
  /* height: 17.4rem; */
  height: 6.4rem;
  background-color: #262626;
  color: #ffffff;
  box-shadow: 0rem 0.3rem 0.5rem 0.2rem rgba(255, 255, 255, 0.2);
  border-radius: 1rem;
  user-select: none;
}

.list-items {
  display: flex;
  width: 100%;
  height: 100%;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.setting-dropmenu ul li {
  width: 100%;
  cursor: pointer;
}

.setting-dropmenu ul li:hover {
  background-color: #7873ff;
}
</style>
