import Vue from "vue";
import Vuex from "vuex";
import dashboards from "./modules/dashboards.js";
import dateRange from "./modules/dateRange.js";
import Tip from "./modules/Tips.js";
import auth from "./modules/auth.js";
import Alert from "./modules/Alerts.js";
import servicesSelect from "./modules/servicesSelect.js";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    isModal: false,
    // isSelect: true,
    isLoading: false,
    isWidgetSettings: false,
    // GoogleSearchConsole || GoogleAnalytics
    widgetType: "",
    isSelectProperty: false,
    isNewsPopup: true && JSON.parse(localStorage.getItem("newsPopup"))
  },
  mutations: {
    OPEN_MODAL: state => {
      state.isModal = true;
      // state.isSelect = true;
    },
    CLOSE_MODAL: state => {
      state.isModal = false;
      // state.isSelect = false;
      state.isWidgetSettings = false;
      // },
      // OPEN_SELECT: state => {
      // state.isSelect = true;
    },
    CLOSE_SELECT: state => {
      state.isModal = false;
      // state.isSelect = false;
    },
    ON_ISLOADING: state => {
      state.isLoading = true;
    },
    SET_ISWIDGETSETTINGS: (state, type) => {
      console.log("Widget Type:", type);
      state.widgetType = type;
      state.isWidgetSettings = true;
      // state.isSelect = false;
    },
    OFF_ISLOADING: state => {
      state.isLoading = false;
    },
    BACK_TO_SERVICES: state => {
      // state.isSelect = true;
      state.isWidgetSettings = false;
    },
    SET_SELECT_PROPERTY: (state, val) => {
      state.isSelectProperty = val;
    },
    SET_ISNEWSPOPUP: (state, val) => {
      state.isNewsPopup = val;
      localStorage.setItem("newsPopup", JSON.stringify(val));
    }
  },
  modules: {
    dashboards,
    dateRange,
    Tip,
    auth,
    Alert,
    servicesSelect
  }
});
