export default {
  fetchLogin: async ({ password, email }) => {
    let body = { password, email };
    try {
      const result = await fetch("https://melytix.herokuapp.com/login", {
        method: "POST",
        headers: {
          "Content-Type": "application/json;charset=utf-8"
        },
        body: JSON.stringify(body)
      })
        .then(res => {
          return res.json();
        })
        .then(data => {
          return data;
        });
      return result;
    } catch ({ message }) {
      return {
        Error: message
      };
    }
  },
  googleAuth: async ({ code, token }) => {
    let body = { code, token };
    try {
      const result = await fetch(
        "https://melytix.herokuapp.com/insert-tokens-main",
        // "https://melytix.herokuapp.com/insert-tokens",
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json;charset=utf-8"
          },
          body: JSON.stringify(body)
        }
      )
        .then(res => {
          return res.json();
        })
        .then(data => {
          return data;
        });
      return result;
    } catch ({ message }) {
      return { Error: message };
    }
  },
  forgotPass: email => {
    const answ = email === "";
    return answ;
  }
};
