import api from "@/api";

export default {
  namespaced: true,
  state: {
    dateFrom: null,
    dateTo: null,
    tempDateFrom: null,
    tempDateTo: null,
    monthName: [
      "Jan.",
      "Feb.",
      "Mar.",
      "Apr.",
      "May",
      "June",
      "July",
      "Aug.",
      "Sept.",
      "Oct.",
      "Nov.",
      "Dec."
    ]
  },
  mutations: {
    CHANGE_DAY: (state, data) => {
      const nDate = new Date(data.dateInMs);
      if (data.calendarName == "from") {
        state.dateFrom = nDate;
        state.tempDateFrom = new Date(
          nDate.getFullYear(),
          nDate.getMonth(),
          15
        );
        if (state.dateTo * 1 - data.dateInMs < 0) {
          state.dateTo = new Date(data.dateInMs + 86400000);
          state.tempDateTo = new Date(
            state.dateTo.getFullYear(),
            state.dateTo.getMonth(),
            15
          );
        }
      } else if (
        data.calendarName == "to" &&
        data.dateInMs - state.dateFrom > 0
      ) {
        state.dateTo = nDate;
        state.tempDateTo = new Date(nDate.getFullYear(), nDate.getMonth(), 15);
      }
    },

    SET_DATE_RANGE: (state, date) => {
      state.dateFrom = date.dateFrom;
      state.dateTo = date.dateTo;
      state.tempDateFrom = date.tempDateFrom;
      state.tempDateTo = date.tempDateTo;
    },

    CHANGE_MONTH: (state, data) => {
      let month = null;
      let year = null;
      if (data.calendarName == "from") {
        month = state.tempDateFrom.getMonth();
        year = state.tempDateFrom.getFullYear();
        state.tempDateFrom =
          data.upOrDown == "up"
            ? new Date(year, month + 1, 15)
            : new Date(year, month - 1, 15);
      } else {
        month = state.tempDateTo.getMonth();
        year = state.tempDateTo.getFullYear();
        state.tempDateTo =
          data.upOrDown == "up"
            ? new Date(year, month + 1, 15)
            : new Date(year, month - 1, 15);
      }
    },

    DROP_TEMPDATE: state => {
      state.tempDateFrom = state.dateFrom;
      state.tempDateTo = state.dateTo;
    }
  },
  actions: {
    GET_DATE_RANGE: ({ commit }) => {
      // fetch the date range from the server
      let dataFromSer = api.fetchDateRange();

      // ----------------------------------------
      let dateFrom = null;
      let dateTo = null;
      if (dataFromSer) {
        dateFrom = dataFromSer.dateFrom;
        dateTo = dataFromSer.dateTo;
      } else {
        const prevDateInMs = new Date() * 1 - 7 * 86400000;
        dateFrom = new Date(prevDateInMs);
        dateTo = new Date();
      }

      const data = {
        dateFrom,
        dateTo,
        tempDateFrom: new Date(dateFrom.getFullYear(), dateFrom.getMonth(), 15),
        tempDateTo: new Date(dateTo.getFullYear(), dateTo.getMonth(), 15)
      };
      commit("SET_DATE_RANGE", data);
    }
  },
  getters: {
    dateFrom(state) {
      return state.dateFrom || state.dateNow;
    },
    dateTo(state) {
      return state.dateTo || state.dateNow;
    },
    dateFromFormat(state) {
      const numM = state.dateFrom.getUTCMonth();
      const numD = state.dateFrom.getDate();
      return `${state.monthName[numM]} ${numD}`;
    },
    dateToFormat(state) {
      const numM = state.dateTo.getUTCMonth();
      const numD = state.dateTo.getDate();
      return `${state.monthName[numM]} ${numD}`;
    },
    tempDateFrom(state) {
      return state.tempDateFrom;
    },
    tempDateTo(state) {
      return state.tempDateTo;
    },
    tempDateFromFormat(state) {
      const num = state.tempDateFrom.getUTCMonth();
      return state.monthName[num];
    },
    tempDateToFormat(state) {
      const num = state.tempDateTo.getUTCMonth();
      return state.monthName[num];
    }
  }
};
