<template>
  <div class="modal-signup">
    <div class="header">
      <div class="btn-close" @click="close"></div>
    </div>
    <div class="title-block">
       
    </div>
    <div class="select-group">
      <span>:{{ name }}</span>
      <select v-model="selectedAccount">
        <option disabled value="">   </option>
        <option v-for="(item, i) in currentData" :key="i" v-bind:value="item">{{
          item.name || item
        }}</option>
      </select>
      <select
        v-if="selectedService == 'GoogleAnalytics'"
        v-model="selectedProperty"
      >
        <option disabled value="">   </option>
        <option
          v-for="(item, i) in selectedAccount.webProperties"
          :key="i"
          v-bind:value="item"
          >{{ item.prop_name }}</option
        >
      </select>
    </div>
    <div id="analytics-signup-btn" @click="handlerOk">
      <span>OK</span>
    </div>
  </div>
</template>
<script>
import { mapState, mapMutations, mapActions } from "vuex";
export default {
  name: "SelectProperty",
  props: ["serviceName"],
  data() {
    return {
      selectedAccount: {},
      selectedProperty: {}
    };
  },
  computed: {
    ...mapState("servicesSelect", [
      "added_services",
      "currentData",
      "selectedService"
    ]),
    name: function() {
      return this.selectedService.replace(/[A-Z]/g, " $&");
    },
    ...mapState("auth", ["token"]),
    currentService: function() {
      const data = this.added_services.find(e => e.name === this.serviceName);
      console.log(data);
      return data.data;
    }
  },
  methods: {
    ...mapActions("servicesSelect", [
      "CLOSE_SELECT_PROPERTY",
      "ADD_SELECTED_PROPERTY"
    ]),
    ...mapMutations(["CLOSE_MODAL"]),
    handlerOk() {
      let options, toSave;
      if (
        (this.selectedAccount.id && this.selectedProperty.prop_id) ||
        this.selectedAccount.length > 0
      ) {
        console.log("ok");
        switch (this.selectedService) {
          case "GoogleAnalytics":
            options = {
              token: this.token,
              account: this.selectedAccount.id || this.selectedAccount,
              web_property: this.selectedProperty.prop_id || null
            };
            toSave = {
              account: this.selectedAccount,
              web_property: this.selectedProperty
            };
            break;
          case "GoogleSearchConsole":
            options = {};
            toSave = { url: this.selectedAccount };
            break;
        }
        console.log("add property ");
        this.ADD_SELECTED_PROPERTY({
          name: this.selectedService,
          body: options,
          data: toSave
        });
        this.CLOSE_MODAL();
        this.CLOSE_SELECT_PROPERTY(this.name);
      }
    },
    close() {
      this.CLOSE_MODAL();
      this.CLOSE_SELECT_PROPERTY(this.name);
    }
  },
  mounted() {}
};
</script>
<style scoped>
.select-group {
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

select {
  color: white;
  font-size: 2.2rem;
  background-color: #262626;
  width: 100%;
  margin: 1rem;
  border-radius: 1rem;
  outline: none;
}

.modal-signup {
  width: 35%;
  border-radius: 0.5vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 1.5vw;
  color: #ffffff;
  padding: 0.5vh 0.5vw;
  user-select: none;
  background-color: #262626;
  box-shadow: 0 0 5px 0.1px #f2f2f2;
}
.header {
  width: 100%;
  display: flex;
  align-items: center;
}
.btn-close {
  width: 1.5vw;
  height: 1.5vw;
  background: url("../../assets/images/modal_widget_settings/btn_close.svg")
    no-repeat center center / contain;
  margin-left: auto;
  cursor: pointer;
  transition: all 0.1s linear;
}
.btn-close:hover {
  filter: drop-shadow(0px 0px 5px rgb(253, 2, 2));
}
#analytics-signup-btn {
  font-size: 1.5vw;
  padding: 0 1.5vw;
  margin: 1vw;
  cursor: pointer;
  background-color: #7873ff;
  border-radius: 0.5vw;
  transition: all 0.1s linear;
}
#analytics-signup-btn:hover {
  box-shadow: 0 0 7px #7873ff;
}
#analytics-signup-btn:active {
  box-shadow: inset 0 0 7px #000000;
  transform: scale(0.95);
}
.title-block {
  display: flex;
  width: 100%;
  border-bottom: 3px solid #7873ff;
}
</style>
