export default (date, tempDate) => {
  // selected date
  const selectDay = date.getDate();
  const selectMonth = date.getMonth();
  const selectYear = date.getFullYear();

  // temp date
  const month = tempDate.getMonth();
  const year = tempDate.getFullYear();
  const firstDateMonth = new Date(year, month, 1, 12);
  const firstDatePrevMonth = new Date(year, month - 1, 1, 12);
  const lastDateMonth = new Date(year, month + 1, 0, 12);
  const lastDatePrevMonth = new Date(year, month, 0, 12);
  const weeks =
    Math.ceil(
      (lastDateMonth.getDate() -
        (lastDateMonth.getDay() ? lastDateMonth.getDay() : 7)) /
        7
    ) + 1;
  const dayInMs = 60 * 60 * 24 * 1000;
  let numDay = 0;
  let calendar = [];
  let firstDateMonthInWeek =
    firstDateMonth.getDay() == 0 ? 7 : firstDateMonth.getDay();
  let startWeekDay = lastDatePrevMonth.getDate() - firstDateMonthInWeek + 2;

  for (let i = 0; i < weeks; i++) {
    let week = [];
    for (let j = 0; j < 7; j++) {
      let day = { isMonth: false, dateInMs: 0, num: 0, isDay: false };
      if (j != firstDateMonthInWeek - 1 && !numDay) {
        day = {
          isMonth: false,
          dateInMs: firstDatePrevMonth * 1 + (startWeekDay - 1) * dayInMs,
          num: startWeekDay++,
          isDay: false
        };
      } else if (j == firstDateMonthInWeek - 1 && !numDay) {
        day = {
          isMonth: true,
          dateInMs: firstDateMonth * 1,
          num: ++numDay,
          isDay:
            selectDay == numDay && selectMonth == month && selectYear == year
              ? true
              : false
        };
      } else if (numDay > 0 && numDay < lastDateMonth.getDate()) {
        day = {
          isMonth: true,
          dateInMs: firstDateMonth * 1 + dayInMs * numDay,
          num: ++numDay,
          isDay:
            selectDay == numDay && selectMonth == month && selectYear == year
              ? true
              : false
        };
      } else if (numDay >= lastDateMonth.getDate()) {
        numDay++;
        day = {
          isMonth: false,
          dateInMs: firstDateMonth * 1 + dayInMs * (numDay - 1),
          num: numDay - lastDateMonth.getDate(),
          isDay: false
        };
      }
      week.push(day);
    }
    calendar.push(week);
  }
  return calendar;
};
