<template>
  <div class="main">
    <div class="date-from-blc">
      <div class="date-title">
        <span class="title">From:</span>
        <div
          :class="[
            isDatepickerFrom
              ? `date-from b-radius-top`
              : `date-from b-radius-all`
          ]"
          @click="handlerDatepicker"
          data-name="from"
        >
          <img
            v-if="isDatepickerFrom"
            src="../../assets/images/datepicker/arrow-up.svg"
            alt="up"
          />
          <span v-if="isDatepickerFrom">{{ tempDateFromFormat }}</span>
          <span v-else>{{ dateFromFormat }}</span>
          <img
            v-if="isDatepickerFrom"
            src="../../assets/images/datepicker/arrow-down.svg"
            alt="down"
          />
        </div>
      </div>
      <Calendar v-if="isDatepickerFrom" :data="calendar" />
    </div>
    <div class="date-to-blc">
      <div class="date-title">
        <span class="title">To:</span>
        <div
          :class="[
            isDatepickerTo ? `date-to b-radius-top` : `date-to b-radius-all`
          ]"
          @click="handlerDatepicker"
          data-name="to"
        >
          <img
            v-if="isDatepickerTo"
            src="../../assets/images/datepicker/arrow-up.svg"
            alt="up"
          />
          <span v-if="isDatepickerTo">{{ tempDateToFormat }}</span>
          <span v-else>{{ dateToFormat }}</span>
          <img
            v-if="isDatepickerTo"
            src="../../assets/images/datepicker/arrow-down.svg"
            alt="down"
          />
        </div>
      </div>
      <Calendar v-if="isDatepickerTo" :data="calendar" />
    </div>
  </div>
</template>

<script>
import { mapGetters, mapMutations, mapActions } from "vuex";
import Calendar from "./Calendar";
import daysInMonth from "@/services/calendar.js";

export default {
  name: "Datepicker",
  components: {
    Calendar
  },
  data() {
    return {
      isDatepickerFrom: false,
      isDatepickerTo: false
    };
  },
  computed: {
    ...mapGetters("dateRange", [
      "dateFrom",
      "dateTo",
      "dateFromFormat",
      "dateToFormat",
      "tempDateFrom",
      "tempDateTo",
      "tempDateFromFormat",
      "tempDateToFormat"
    ]),
    calendar() {
      if (this.isDatepickerFrom) {
        const dName = "from";
        return {
          calendar: this.getCalendar(dName),
          dName,
          handlerClick: this.CHANGE_DAY
        };
      } else {
        const dName = "to";
        return {
          calendar: this.getCalendar(dName),
          dName,
          handlerClick: this.CHANGE_DAY
        };
      }
    }
  },
  methods: {
    ...mapMutations("dateRange", [
      "CHANGE_DAY",
      "CHANGE_MONTH",
      "DROP_TEMPDATE"
    ]),
    ...mapActions("dateRange", ["GET_DATE_RANGE"]),
    // handlerDatepicker(ev) {
    //   const target = ev.target;
    //   if (
    //     (target.classList.contains("date-from") ||
    //       target.parentNode.classList.contains("date-from")) &&
    //     target.tagName !== "IMG"
    //   ) {
    //     if (this.isDatepickerFrom) {
    //       this.isDatepickerFrom = false;
    //     } else {
    //       this.isDatepickerFrom = true;
    //       this.isDatepickerTo = false;
    //     }
    //   } else if (
    //     (target.classList.contains("date-to") ||
    //       target.parentNode.classList.contains("date-to")) &&
    //     target.tagName != "IMG"
    //   ) {
    //     if (this.isDatepickerTo) {
    //       this.isDatepickerTo = false;
    //     } else {
    //       this.isDatepickerTo = true;
    //       this.isDatepickerFrom = false;
    //     }
    //   } else if (target.tagName == "IMG") {
    //     const calendarName = target.parentNode.dataset.name;
    //     this.CHANGE_MONTH({ upOrDown: target.alt, calendarName });
    //   }
    // },
    handlerDatepicker() {},
    getCalendar(calName) {
      const date = calName == "from" ? this.dateFrom : this.dateTo;
      const tempDate = calName == "from" ? this.tempDateFrom : this.tempDateTo;
      return daysInMonth(date, tempDate);
    }
  },
  watch: {
    isDatepickerFrom() {
      this.DROP_TEMPDATE();
    },
    isDatepickerTo() {
      this.DROP_TEMPDATE();
    }
  },
  beforeMount() {
    this.GET_DATE_RANGE();
  }
};
</script>

<style scoped>
.main {
  display: flex;
  align-items: center;
  user-select: none;
}

.date-title {
  margin-left: 2.5rem;
  display: flex;
  align-items: center;
}

.title {
  color: #ffffff;
  font-size: 2.8rem;
  font-weight: 100;
}

.date-to,
.date-from {
  width: 10.5rem;
  background-color: #262626;
  padding: 0.5rem 1rem;
  margin-left: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.b-radius-all {
  border-radius: 1rem;
}

.b-radius-top {
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;
}

.date-to span,
.date-from span {
  color: #7873ff;
  font-size: 2.2rem;
}

.date-to img,
.date-from img {
  cursor: pointer;
}

.date-from-blc:hover .date-from,
.date-to-blc:hover .date-to {
  box-shadow: 0rem -0.2rem 0.5rem 0.2rem rgba(255, 255, 255, 0.2);
  opacity: 1;
}
</style>
